export default {
  data() {
    return {
      xtzPriceArray: '',
      xtzPrice: '',
      timer: '',
      active: 1,
      activeFive: 1,
      activeTime: 'all',
      chartOptions: {
        chart: {
          id: 'tvl',
          fontFamily: 'Cera Pro, sans-serif',
          foreColor: '#000',
          toolbar: {
            tools: {
              download: false,
              selection: false,
              zoom: false,
              pan: false,
              reset: false,
            },
          },
        },
        title: {
          text: 'Tezos Price',
          offsetY: 14,
          margin: 0,
          style: {
            fontSize: '1.3rem',
            fontWeight: 'regular',
            color: '#000',
          },
        },
        xaxis: {
          type: 'datetime',
          axisBorder: {
            show: false,
          },
          labels: {
            rotate: 0,
            style: {
              fontSize: '1rem',
            },
            datetimeFormatter: {
              year: 'yyyy',
              month: "MMM 'yy",
              day: 'dd MMM.',
              hour: 'HH:mm',
            },
          },
          tooltip: {
            style: {
              fontSize: '1rem',
            },
          },
          crosshairs: {
            stroke: {
              dashArray: 7,
            },
          },
        },
        yaxis: {
          tooltip: {
            enabled: true,
          },
          crosshairs: {
            stroke: {
              dashArray: 7,
            },
          },
        },
        plotOptions: {
          candlestick: {
            colors: {
              upward: '#27e4bb',
              downward: '#f3566b',
            },
          },
        },
        grid: {
          borderColor: '#c8c8c8',
          strokeWidth: '2',
          strokeDashArray: 14,
          xaxis: {
            lines: {
              show: true,
            },
          },
        },
        tooltip: {
          enabled: true,
          theme: 'dark',
        },
      },
      series: [
        {
          data: [
            {
              x: new Date(1538778600000),
              y: [6629.81, 6650.5, 6623.04, 6633.33],
            },
          ],
        },
      ],
    }
  },
  created() {
    this.fetchXtzPrice()
    this.timer = setInterval(this.fetchXtzPrice, 600000)
    this.fetchCandleData()
    console.log('graph', this.xtzPriceArray)
  },
  methods: {
    async fetchCandleData() {
      const xtzPriceArray = await fetch(
        `https://xtz.cbddev.com/xtz-price-output.php`
      ).then((res) => res.json())
      this.xtzPriceArray = await xtzPriceArray
      // eslint-disable-next-line no-eval
      this.xtzPriceArray = eval(
        JSON.stringify(this.xtzPriceArray).replace(/['"]+/g, '')
      )
      console.log(this.xtzPriceArray)
      /* this.$refs.tvl.updateSeries([
        {
          name: 'Tezos Price',
          // eslint-disable-next-line no-eval
          data: eval(this.xtzPriceArray),
        },
      ]) */
    },
    async fetchXtzPrice() {
      const xtzPrice = await fetch(
        `https://api.teztools.io/token/xtz-price`
      ).then((res) => res.json())
      this.xtzPrice = await xtzPrice.price
    },
    zoomGraph(time) {
      this.activeTime = time
      return time === '24h'
        ? this.$refs.tvl.zoomX(
            new Date(Date.now() - 1 * 24 * 60 * 60 * 1000).getTime(),
            new Date().getTime()
          )
        : time === '7d'
        ? this.$refs.tvl.zoomX(
            new Date(Date.now() - 7 * 24 * 60 * 60 * 1000).getTime(),
            new Date().getTime()
          )
        : time === '30d'
        ? this.$refs.tvl.zoomX(
            new Date(Date.now() - 30 * 24 * 60 * 60 * 1000).getTime(),
            new Date().getTime()
          )
        : time === '1y'
        ? this.$refs.tvl.zoomX(
            new Date(Date.now() - 365 * 24 * 60 * 60 * 1000).getTime(),
            new Date().getTime()
          )
        : this.$refs.tvl.resetSeries()
    },
  },
}
